@import "./variables.scss";

//
figure {
  margin: 0 16px 0 10px;
}

ul li {
  list-style: none;
}

//
body {
  overflow-x: hidden;
  font-family: "Montserrat", sans-serif;
}

.user {
  background-color: #191919;
}

.container {
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
  padding-left: $gutter;
  padding-right: $gutter;
}

.container-2 {
  // max-width: 820px;
  // padding: 0 40px;
}

.user__social-icon {
  display: flex;
  justify-content: space-between;
  padding: 12px 0;
  background-color: #191919;
}

.user__social {
  list-style: none;
  display: flex;
  align-items: center;
  li {
    padding: 0 8px;
    .fa {
      color: #707070;
    }
  }
}

.article__desc {
}

.user {
  &__info {
    display: flex;
    color: #ffffff;
    opacity: 0.5;
    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    font-variant: small-caps;
    letter-spacing: 0.8px;
  }
  &__details {
    padding: 0px 12px 0 0;
  }
  &__line {
    width: 1px;
    height: 24px;
    background: #b4b4b4;
    opacity: 0.38;
  }
  &__lang {
    padding: 0 0 0 16px;
  }
}

.heading {
  background: url("/img/Layer\ 66@1X.png");
  &__top {
    display: flex;
    padding: 20px 0;
    justify-content: space-between;
  }
  &__logo {
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    margin: -6px 8px;
    text-transform: uppercase;
    letter-spacing: 1.92px;
    cursor: pointer;
  }
  &__icons {
    display: flex;
    align-items: center;
    ul {
      display: flex;
      li {
        list-style: none;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.7px;
        padding: 0 0 26px 18px;
        img {
          padding: 0 0 0 11px;
        }
        a {
          text-decoration: none;
          color: #ffffff;
        }
        &:hover {
          border-bottom: 4px solid #ff4081;
          padding: 0 0 22px 18px;
        }
      }
    }
    .nav-active {
      // border-bottom: 4px solid #ff4081;
      // padding: 0 0 25px 17px;
    }
  }
  &__info {
    display: flex;
    .fa {
      color: #fff;
      padding: 0 17px 0 17px;
      cursor: pointer;
    }
  }
}

.article {
  &__desc {
    text-align: center;
    padding: 188px 0 68px 0;
  }
  &__author {
    &-avatar {
      margin: 27px -17px 0px -6px;
    }
  }
  &__tag {
    margin: 0;
    opacity: 0.7;
    color: #ffffff;
    font-weight: 400;
    line-height: 27px;
    font-variant: small-caps;
    font-size: 27px;
  }
  &__title {
    color: #ffffff;
    font-size: 42px;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    margin: 16px 0 34px 0;

    &__title {
    }
  }
  &__button {
    margin: 0 0 50px 0;
  }
  &__author-avatar {
    img {
      border-radius: 50%;
    }
  }
  &__author-name {
    color: #ffffff;
    font-size: 14px;
    font-weight: 700;
    line-height: 27px;
    letter-spacing: 0.7px;
    margin: 7px 0px 0 16px;
  }
  &__date {
    color: #ffffff;
    font-size: 12px;
    font-weight: 400;
    // line-height: 27px;
    opacity: 0.5;
    letter-spacing: 0.6px;
  }
  &__activity {
    display: flex;
    justify-content: center;
    opacity: 0.7;
    color: #ffffff;
    font-size: 12px;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0.6px;
    margin: 29px 0 0 0;
  }
  &__comments {
    margin: 0 -8px 0 0px;
  }
  &__likes {
    padding: 0px 24px 0 0px;
    margin: 0px -3px 0 34px;
  }
}

.button {
  font-size: 24px;
  line-height: 36px;
  font-variant: small-caps;
  letter-spacing: 1.2px;
  color: #fff;
  box-shadow: 0 17px 24px rgba(48, 63, 159, 0.2);
  border-radius: 22px;
  background-color: #303f9f;
  border: none;
  padding: 3px 25px 7px 25px;
  cursor: pointer;
}

.main {
  background-color: #f5f5f5;

  &__nav-wrapper {
    background-color: #fff;
    // margin: 10px 0 0 15px;
  }

  &__nav-label {
    opacity: 0.87;
    color: #000000;
    font-size: 16px;
    font-weight: 700;
    line-height: 27px;
    font-variant: small-caps;
    letter-spacing: 0.8px;
    margin: 0 22px 0 0;
  }

  &__nav {
    display: flex;
    padding: 12px 0 13px 5px;

    &-tags {
      list-style: none;
      display: flex;
    }
    &-tag {
      margin: 0px 18px 0 0;
      padding: 0px 27px;
      border-radius: 10px;
      cursor: pointer;
      opacity: 0.87;
      color: #000000;
      font-size: 16px;
      font-weight: 400;
      line-height: 27px;
      font-variant: small-caps;
      letter-spacing: 0.8px;
      border-radius: 15px;
      background-color: #f5f5f5;
      // margin: 0 15px 0 0;
      &--active {
        color: #3f51b5;
        font-size: 16px;
        font-weight: 400;
        line-height: 27px;
        font-variant: small-caps;
        letter-spacing: 0.8px;
        box-shadow: 0 1px 0 #dddddd;
        background-color: #ffffff;
        border: 1px solid #3f51b5;
      }
    }
  }

  &__content {
    padding: 30px 0 0 0;
  }
  &__wrapper {
    display: flex;
  }
}

