.sidebar {
  &__main {
    display: flex;
    flex-direction: column;
    margin: 30px 0px 0 28px;
    background: #fff;
    padding: 0 0px 0 37px;
  }
  &__title {
    color: #3f51b5;
    font-size: 17px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.15px;
    border-bottom: 1px solid #2962ff;
    padding: 30px 0 7px 0;
    margin: 1px 0px 0px 0px;
  }

  &__search {
    position: relative;
    padding: 23px 0px 0px 0;
    > input {
      border: none;
      padding: 14px 90px 11px 15px;
      border-radius: 23px;
      background: #f5f5f5;
      color: #a4a4a4;
      letter-spacing: 0.64px;
    }

    .fa-search {
      color: #ffffff;
      position: absolute;
      top: 26px;
      right: 5px;
    }
    .fa {
      background: #ff4081;
      padding: 9px;
      border-radius: 50%;
      box-shadow: 0 2px 13px rgba(0, 0, 0, 0.12);
    }
  }

  &__categories {
    &-title {
      span {
        opacity: 0.87;
        color: #000000;
        font-size: 14px;
        font-weight: 700;
        padding: 16px 0;
        letter-spacing: 0.56px;
      }
    }
    &_item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #ebeaea;
      padding: 15px 3px 8px 0px;
      font-size: 13px;
      span {
        display: flex;
        opacity: 0.87;
        color: #000000;
        font-size: 14px;
        font-weight: 700;
        // padding: 16px 0;
        letter-spacing: 0.56px;
      }
    }
  }

  &__subcribe {
    margin: 10px 0px -12px 0px;
    &_name {
      margin: 15px 0px 14px 0px;
    }

    &_email {
      margin: 15px 0px 12px 0;
    }
    &_input {
      padding: 12px 92px 12px 12px;
      border-radius: 3px;
      border: 1px solid #e0e0e0;
    }
    &_button {
      border-radius: 4px;
      background-color: #3f51b5;
      color: #ffffff;
      font-size: 24px;
      font-weight: 400;
      line-height: 36px;
      font-variant: small-caps;
      letter-spacing: 1.2px;
      padding: 5px 84px;
      margin: 8px 1px 24px 0px;
      position: relative;
      border: none;
      cursor: pointer;
    }
    &_line {
      height: 1px;
      background-color: #000;
      position: relative;
      span {
        position: absolute;
        bottom: -14px;
        right: 44%;
        background: #fff;
        padding: 7px 11px;
        border-radius: 50%;
      }
    }
    &_icons {
      margin: 24px 0 0 0;
      ul {
        display: flex;
        .fa {
          border-radius: 50%;
          height: 36px;
          width: 36px;
          font-size: 18px;
          line-height: 36px;
          text-align: center;
        }
        .fa-facebook {
          color: #ffffff;
          background: #3b5999;
        }
        .fa-twitter {
          color: #ffffff;
          background: #55acee;
        }
        .fa-vk {
          color: #ffffff;
          background: #4c75a3;
        }
        .fa-pinterest {
          color: #ffffff;
          background: #ff2e56;
        }
        .fa-tumblr {
          color: #ffffff;
          background: #34465d;
        }
        .fa-dribbble {
          color: #ffffff;
          background: #ea4c89;
        }
      }
      &-items {
        padding: 0 9px 0 0;
      }
    }
  }

  &__post {
    &_buttons {
      display: flex;
      margin: 25px 0 0 0;
      border: 1px solid #3f51b5;
      border-radius: 4px;
    }
    &_button {
      font-size: 14px;
      font-variant: small-caps;
      letter-spacing: 0.8px;
      padding: 11px 22px 10px 26px;
      // margin: 7px 0px 0px 0px;
      border-radius: 4px 0 0 4px;
      cursor: pointer;
      &-active {
        color: #fff;
        background-color: #3f51b5;
        // padding: 11px 30px 0px 23px;
      }
    }
    &_content {
      padding: 0px 0 14px 0;
      border-bottom: 1px solid #dee5e9;
      &:nth-child(4) {
      }
    }
    &_content:nth-child(6) {
      border-bottom: none;
    }
    &_desc {
      margin: 15px 0 0 0;
      display: flex;
      img {
      }
    }

    &_info {
      padding: 0px 0px 0 12px;
      .fa {
        color: #6f7dc8;
        margin: 0px 6px 0 0;
        font-size: 14px;
      }
    }

    &_date {
      display: flex;
      justify-content: space-between;
      letter-spacing: -0.5px;
    }
    &_time {
      font-size: 13px;
    }
    &_title {
      font-weight: 600;
      padding: 5px 0 0 0;
    }
    &-title {
      margin: 16px 0px -8px 0px;
    }
  }

  &__tags {
    &_list {
      margin: 15px 0 0 0;
      ul {
        display: flex;
        flex-wrap: wrap;
      }
    }
    &_item {
      background-color: #eeeeee;
      padding: 7px 24px 5px 13px;
      margin: 5px 10px 6px 0px;
      font-size: 14px;
      border-radius: 4px;
      font-weight: 600;
    }
  }
}

