.footer {
  &__main {
    background-color: #212121;
  }
  &__title {
    color: #ffffff;
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1.15px;
    padding: 0 0 24px 0;
    &_social {
      // padding: 0px 0 15px 0;
    }
  }
  &__widget {
    padding: 50px 0 70px 0;
    display: flex;
    &_pages {
      padding: 0 0px 18px 8px;
      margin: 0 8px 0 0;
    }
    &_accordion {
      .accordion {
        background: transparent;
        color: #bdbdbd;
        cursor: pointer;
        padding: 15px 0px 12px 18px;
        width: 100%;
        text-align: left;
        outline: none;
        transition: 0.4s;
        font-size: 14px;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 0.7px;
        border-radius: 4px;
        border: 1px solid #ffffff;
        opacity: 0.38;
        margin: 0px 87px -1px 0;
        display: flex;
        justify-content: space-between;
      }

      .panel {
        padding: 0 18px;
        background: transparent;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.2s ease-out;
        color: #595959;
        margin: 0 0 7px 0;
        border-bottom: 1px solid #757575;
        border-right: 1px solid #757575;
        border-left: 1px solid #757575;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        p {
          font-size: 14px;
        }
      }

      .accordion:after {
        content: "\02795";
        font-size: 13px;
        color: #909090;
        float: right;
        margin-left: 45px;
        margin-right: 10px;
      }

      .active:after {
        content: "\2796";
      }
    }

    &_combined {
      // width: 25%;
      padding: 0 0px 0 2px;
      margin: 0 0px 0 29px;
    }
    &_twitter {
      margin: 0px 0px 23px -10px;
    }
    &_info {
      margin: 0 0 0 0;

      &:nth-child(3) {
        border-bottom: 1px solid #a9a9a9;
        border-top: 1px solid #a9a9a9;
        padding: 16px 0 3px 0;
      }
      img {
        height: 100%;
        margin: 0px 0px 0 8px;
      }
      &-3 {
        margin: 12px 0 0 0;
        img {
          height: 100%;
          margin: 0px 0px 0 8px;
        }
      }
    }

    &_breadcrumb {
      span {
        color: #a9a9a9;
        opacity: 0.7;
        font-size: 12px;
        font-weight: 400;
        line-height: 36px;
        letter-spacing: 0.6px;
        margin: 0 3px 0 0px;

        &:nth-child(n + 3) {
          border-bottom: 1px solid;
        }
      }
    }

    &_icon {
      display: flex;
      P {
        margin: -4px 0 0 0;
        color: #cecece;
        font-size: 14px;
        font-size: 13px;
        letter-spacing: 0.52px;
        letter-spacing: 0.56px;
        padding: 0px 0px 0px 22px;
        line-height: 20px;
      }
    }

    &_social {
      padding: 0 0px 0px 0px;
      margin: -5px 0px 0px -5px;

      ul {
        display: flex;
        .fa {
          border-radius: 50%;
          height: 36px;
          width: 36px;
          font-size: 18px;
          line-height: 36px;
          text-align: center;
        }
        .fa-facebook {
          color: #ffffff;
          background: #3b5999;
        }
        .fa-twitter {
          color: #ffffff;
          background: #55acee;
        }
        .fa-vk {
          color: #ffffff;
          background: #4c75a3;
        }
        .fa-pinterest {
          color: #ffffff;
          background: #ff2e56;
        }
        .fa-tumblr {
          color: #ffffff;
          background: #34465d;
        }
        .fa-dribbble {
          color: #ffffff;
          background: #ea4c89;
        }
      }
      &-items {
        padding: 0 9px 0 0;
      }
    }

    &_flickr {
      // width: 25%;
      margin: 1px 0px 0 32px;
    }

    &_img {
      img {
        padding: 1px;
      }
    }

    &_news {
      // width: 25%;
      // margin: 0 -43px 0 0px;
      // margin: 0 -31px 0 0px;
    }
    &_form {
      display: flex;
      flex-direction: column;
      margin: 0 15px 0 0;

      input {
        padding: 15px 0px 12px 14px;
        color: #909090;
        margin: 1px -14px 10px -1px;
        background: none;
        border: 1px solid #909090;
        border-radius: 3px;
      }
      &-button {
        padding: 10px 20px 10px 20px;
        margin: 10px -15px 0px 0px;
        background: #303030;
        border: transparent;
        color: #fff;
        border-radius: 3px;
        font-size: 12px;
        text-transform: uppercase;
        cursor: pointer;
      }
    }

    &-name {
    }
    &-button {
      // margin: 10px 0 0 0;
      // padding: 10px 30px;
      // background: #303030;
      // color: #fff;
      // border: none;
      // text-transform: uppercase;
      // cursor: pointer;
      // font-size: 14px;
    }
    .textarea {
      padding: 16px 26px 0px 15px;
      margin: 0px -15px 1px -1px;
      color: #909090;
      background: none;
      border: 1px solid #909090;
      border-radius: 3px;
      height: 105px;
      color: #909090;
    }
  }
}

